@keyframes continuousAnimation {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(10);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .BlogCard {
    /* Add your regular styles for the BlogCard component */
  
    /* Add animation styles */
    animation: continuousAnimation 1s infinite;
  }
  